<template>
  <div v-if="connection">
    <div class="dailer-pad position-relative" role="group" aria-label="Basic example">
      <!-- Input group starts here -->
      <div class="input-group phone-pad-section align-items-center w-100">
        <input placeholder="Enter phone number" class="bg-transparent border-0 font-weight-normal form-control h3 mb-0 p-0 text-center phno-placeholder text-white" v-model="phone_number" />
        <i class="fal fa-arrow-up pb-1" />
      </div>
      <b-list-group>
        <b-list-group-item class="rounded-0 p-0 border-left-0 border-right-0 border-light d-flex ">
          <div class="flex-grow-1 border-right">
            <b-button
              variant="transparent"
              class="text-center text-white rounded-0 d-flex flex-column align-items-center h-100 keyboard-number justify-content-center"
              v-b-modal.conferenceParticipants
              block
            >
              <span class="fe fe-user-plus" />
              <small>Add</small>
            </b-button>
          </div>
          <div class="flex-grow-1 border-right">
            <b-button
              variant="transparent"
              class="text-center text-white rounded-0 d-flex flex-column align-items-center h-100 keyboard-number justify-content-center"
              @click="$emit('showkeypad')"
              block
            >
              <span class="fe fe-grid" />
              <small>Keypad</small>
            </b-button>
          </div>
          <div class="flex-grow-1 ">
            <b-button
              variant="transparent"
              class="text-center text-white rounded-0 d-flex flex-column align-items-center h-100 keyboard-number justify-content-center"
              @click="
                $emit('mutecall');
                is_mute = !is_mute;
              "
              block
            >
              <i :class="`fe ${is_mute ? 'fe-mic' : 'fe-mic-off text-danger'}`" />
              <small v-if="is_mute">Mute</small>
              <small class="text-danger" v-else>unmute</small>
            </b-button>
          </div>
        </b-list-group-item>
      </b-list-group>
    </div>

    <b-modal size="sm" id="conferenceParticipants" content-class="rounded-sm" @show="getParticipants()" body-class="p-3 rounded-sm height-93vh" hide-header hide-footer title="Add to conference">
      <ConferenceList
        ref="conference_list_ref"
        :participants="participants"
        :conference_sid="conference_sid"
        @updateParticipants="updateParticipants"
        :twilioConfig="twilioConfig"
        :isLoading="isLoading"
        :from_number="parseInt(from_number)"
      />
    </b-modal>
  </div>
</template>

<script>
  import conference from "../../../js/dialer/conference";
  import ConferenceList from "./conference/ConferenceList.vue";
  export default {
    props: {
      connection: Object,
      twilioMute: Boolean,
      twilioConfig: Object,
      call_sid: String,
    },
    components: { ConferenceList },
    data() {
      return {
        phone_number: "",
        conference_name: "",
        conference_sid: "",
        from_number: "",
        is_mute: true,
        participants: [],
        participants_contacts: [],
        isLoading: true,
      };
    },
    mounted: function() {
      this.is_mute = !this.twilioMute;
    },
    methods: {
      // Get All participants currently in conference
      async getParticipants() {
        this.isLoading = true;
        let data = await conference.getParticipants(this.conference_sid, this.twilioConfig);
        this.from_number = data.from_number;
        data = data.participants;
        this.participants = [];

        // Check for participants and append phone number based on call_sid
        this.participants_contacts.filter((participant) => {
          for (let item in data) {
            if (data[item].call_sid == participant.call_sid) {
              data[item].phone_number = participant.phone_number;
              this.participants.push(data[item]);
            }
          }
        });

        // Stop loader
        this.isLoading = false;
      },

      // Listen to addParticipant child event
      async updateParticipants(data) {
        // Push data on adding new participants
        if (data.type == "add") {
          this.participants_contacts.push(data);
          this.participants.push(data);
        } else if (data.type == "update") {
          data.phone_number = this.participants[data.index].phone_number;
          this.participants[data.index] = data;
        } else if (data.type == "delete") {
          this.participants.splice(data.index, 1);
        }
        this.$refs.conference_list_ref.$refs.conference_list_table.refresh();
      },
    },
  };
</script>
<style>
  .text-danger {
    color: #ef4b69 !important;
  }
  .height-93vh {
    max-height: 93vh;
  }
</style>
